import { useEffect, useState } from 'react';
import { RecordApi } from '../../utils/adminApi';
import { Table } from 'antd';
const { Column } = Table;

const Record = () => {
  const [tableData, setTableData] = useState();

  useEffect(() => {
    RecordApi().then((res) => {
      setTableData(
        res.data.map((item) => ({
          ...item,
          id: item._id.slice(0, 7),
          createdAt: item.createdAt.replace(/[TZ]/g, ' '),
          updatedAt: item.updatedAt.replace(/[TZ]/g, ' '),
        }))
      );
    });
  }, []);


  return (
    <Table dataSource={tableData} rowKey="_id">
      <Column title="ID" dataIndex="id" />
      <Column title="机器人编号" render={(_,record) => (<p>
      	{record.product._id}
      </p>)} />
      <Column title="机器人名称" render={(_, record) => (<p>
      	{record.product.name}
      </p>)} />
      <Column title="会员" render={(_,record) => (<p>
      	{record.user.phone}
      </p>)} />
      <Column title="已发放收益" dataIndex="done" />
      <Column title="收益/每小时" dataIndex="income" />
      <Column title="购买价格" dataIndex="price" />
      <Column title="购买时间" dataIndex="createdAt" />
      <Column title="运行时间" dataIndex="updatedAt" />
      <Column title="状态" render={(_,record) => (<p>
      	{record.state ? '运行中': "已结束"}
      </p>)}/>
    </Table>
  );
};

export default Record;
